@import "../../_globalColor";

.card-image {
  max-width: 100%;
  height: auto;
  width: 100%; // Adjusted to match achievement card
}

.card-title {
  color: $titleColor;
  font-size: 1.375rem; // Matching achievement card font size
  line-height: 24px;
  font-weight: 700;
  margin: 15px 0 0 0;
}

.card-subtitle {
  color: $cardSubtitle;
  font-size: 1.063rem;
  line-height: 1.5rem;
}

.project-card {
  display: flex;
  flex-direction: column; // Ensures consistent structure
  justify-content: space-between; /* Ensures items distribute evenly */
  //height: 100%; /* Ensure consistent height for all cards */
  background-color: $lightBackground2;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  padding: 1rem;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
  transition: all 0.3s ease;
}

.project-detail {
  flex-grow: 1; /* Pushes the footer down */
  display: flex;
  flex-direction: column;
}

.project-card:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
}

.dark-mode .project-card:hover {
  box-shadow: $achievementCertificateCardHoverDark 0px 20px 30px -10px;
}

.project-image {
  position: relative;
  height: 400px; // Matching certificate image height
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 768px) {
    height: auto; // Fix white space on smaller screens
  }
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

.project-card-footer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: auto; // Pushes tags to the bottom
  //padding-top: 10px;
}

.project-card-footer span.project-tag {
  background: $buttonColor;
  color: $lightBackground3;
  vertical-align: middle;
  align-items: center;
  border-radius: 4px;
  display: inline-flex;
  font-size: 0.75rem;
  height: 2em;
  justify-content: center;
  white-space: nowrap;
  line-height: 1.5;
  margin: 0 0.5rem 0.5rem 0;
  padding: 0 0.75em;
  cursor: pointer;
  transition: 0.2s ease-in;
}

span.project-tag:hover {
  background: $buttonHover;
}
