@import "../../_globalColor";

.research-heading {
  font-size: 56px;
  font-weight: 400;
  line-height: normal;
  margin: 0px;
}
.research-subtitle {
  text-transform: uppercase;
  margin: 0px;
  margin-bottom: 50px;
}
.subTitle {
  text-transform: None;
  color: $subTitle;
}

/* Media Query */
@media (max-width: 1380px) {
  .research-heading {
    font-size: 40px;
  }
}
@media (max-width: 768px) {
  .research-heading {
    font-size: 30px;
    text-align: left;
  }
  .research-subtitle {
    font-size: 16px;
    line-height: normal;
    text-align: left;
  }
}
@media (max-width: 300px) {
  .research-heading {
    font-size: 25px;
    text-align: left;
  }
  .research-subtitle {
    font-size: 12px;
    line-height: normal;
    text-align: left;
  }
}