.education-card{display:flex;flex-direction:row}.education-card:hover+.education-card-border{border-color:#b3a369;width:100%;-webkit-transition:width .6s ease-in-out;-moz-transition:width .6s ease-in-out;-o-transition:width .6s ease-in-out;transition:width .6s ease-in-out}.education-card-border{height:30px;width:80%;border-top:2px solid rgba(211,211,211,.397);margin-bottom:20px;-webkit-transition:width .6s ease-in-out;-moz-transition:width .6s ease-in-out;-o-transition:width .6s ease-in-out;transition:width .6s ease-in-out}.education-head{display:flex;flex-direction:row}.education-text-school{margin:0;font-size:28px;font-weight:700;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;height:27px}.education-roundedimg{object-fit:contain;margin-right:2rem;width:6rem;height:6rem;box-shadow:0 .5rem 1rem rgba(0,0,0,.3);border-radius:50%}.education-card-right{max-width:70%}.education-text-subHeader{color:#000;font-weight:700;font-size:19px;margin:0px;padding-top:.8rem;line-height:normal}.education-text-duration{color:#000;font-size:19px;margin:0px;padding-top:.5rem}.education-text-desc{margin-top:.5rem;margin-bottom:.5rem}.education-text-bullets>ul{text-align:left;margin-top:.5rem}.education-text-bullets>ul li::before{content:"";color:#000;font-size:16px;position:absolute;left:0;top:0}@media(max-width: 1380px){.education-card{flex-direction:column;padding-bottom:10px}.education-card-right{max-width:90%}.education-card-border{width:90%}.education-text-subHeader{padding-top:.5rem;font-size:16px}.education-text-school{font-size:20px;height:23px}.education-text-duration{font-size:16px}.education-roundedimg{width:4rem;height:4rem;margin-bottom:.5rem;margin-right:0px}}@media(max-width: 768px){.education-card{text-align:center}.education-card-right{max-width:100%}.education-card-border{width:100%}.education-roundedimg{margin-top:10px;width:4rem;height:4rem;margin-bottom:1rem;margin-right:0}.education-text-bullets>ul{max-width:80%;margin:auto}}